<template>
  <div class="section">
    <div class="section__background">
      <img id="backgroundImage" class="section__background-image" v-bind:src="currentMusic.photo" alt="">
    </div>
    <div class="music-card__wrapper">
      <div class="music-card" ref="musicCard">
        <div class="music-card__content">
          <img class="music-image" ref="musicCover" v-bind:src="currentMusic.photo" alt="">
          <div class="music-info">
            <h2 class="music-name">{{ currentMusic.title }}</h2>
            <p class="music-artist">{{ currentMusic.madah }}</p>
          </div>
          <div class="music-progress">
            <div id="progress-bar" class="music-progress-bar"></div>
            <div class="music-progress__time">
              <span class="music-progress__time-item music-current-time">{{ currentTime }}</span>

              <span class="music-progress__time-item music-duration-time"  v-if="isPlaying">{{ durationTime }}</span>
              <span class="music-progress__time-item music-duration-time"  v-else>00:00</span>
            </div>
          </div>
          <div class="music-controls">
            <div id="prev" class="music-controls-item" @click="prevMusic">
              <i class="fas fa-backward music-controls-item--icon"></i>
            </div>

            <div id="play" class="music-controls-item" @click="isPlaying ? pauseMusic() : playMusic() ">
              <i class="fas fa-play music-controls-item--icon play-icon"></i>
              <div class="play-icon-background"></div>
            </div>

            <div id="next" class="music-controls-item" @click="nextMusic">
              <i class="fas fa-forward music-controls-item--icon"></i>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import {ref} from 'vue'
import musics from "./musics";
export default {
  name: 'App',
  setup() {
    const currentMusic = ref(null);
    const musicCover = ref(null);
    const musicCard = ref(null);
    const audio = ref(null);
    const currentMusicIndex = ref(0);
    const currentTime = ref('');
    const durationTime = ref('');
    let isPlaying = ref(false);

    currentMusic.value = musics[currentMusicIndex.value];
    audio.value = new Audio();
    audio.value.src = currentMusic.value.source;


    const playMusic = () => {
      audio.value.play();
      document.querySelector('.play-icon').classList.replace('fa-play', 'fa-pause')
      isPlaying.value = true;
      fadeInCover();
      musicCard.value.classList.add('middle-weight');
      setTimeout(() => {
        musicCard.value.classList.remove('middle-weight');
      }, 200)
    }

    const pauseMusic = () => {
      audio.value.pause();
      document.querySelector('.play-icon').classList.replace('fa-pause', 'fa-play')
      isPlaying.value = false;
      fadeInCover();
      musicCard.value.classList.add('middle-weight');
      setTimeout(() => {
        musicCard.value.classList.remove('middle-weight');
      }, 200)
    }

    const fadeInCover = () => {
      musicCover.value.classList.add('animate')
      setTimeout(() => {
        musicCover.value.classList.remove('animate')
      }, 300)
    }

    const nextMusic = () => {
        if(currentMusicIndex.value < musics.length - 1) {
          currentMusicIndex.value += 1;
        }
        else {
          currentMusicIndex.value = 0;
        }
        currentMusic.value = musics[currentMusicIndex.value];
        audio.value.src = currentMusic.value.source;
        if(isPlaying.value) {
          playMusic();
        }
        else{
          pauseMusic();
        }
        musicCard.value.classList.add('right-weight');
        setTimeout(() => {
          musicCard.value.classList.remove('right-weight');
        }, 200)
    }

    const prevMusic = () => {
      if(currentMusicIndex.value === 0) {
        currentMusicIndex.value = musics.length - 1;
      }
      else {
        currentMusicIndex.value -= 1;
      }
      currentMusic.value = musics[currentMusicIndex.value];
      audio.value.src = currentMusic.value.source;
      if(isPlaying.value) {
        playMusic();
      }
      else{
        pauseMusic();
      }
      musicCard.value.classList.add('left-weight');
      setTimeout(() => {
        musicCard.value.classList.remove('left-weight');
      }, 200)
    }

    const setMusicTime = () => {
      let durMin = Math.floor(audio.value.duration / 60);
      let durSec = Math.floor(audio.value.duration - durMin * 60);

      let curMin = Math.floor(audio.value.currentTime / 60);
      let curSec = Math.floor(audio.value.currentTime - curMin * 60);

      if(durMin < 10) {
        durMin = '0' + durMin;
      }
      if(durSec < 10) {
        durSec = '0' + durSec;
      }
      if(curMin < 10) {
        curMin = '0' + curMin;
      }
      if(curSec < 10) {
        curSec = '0' + curSec;
      }
      durationTime.value = durMin + ':' + durSec;
      currentTime.value = curMin + ':' + curSec;
    }

    audio.value.onloadedmetadata= () => {
      setMusicTime();
    }

    audio.value.ontimeupdate= () => {
      setMusicTime();
    }

    return {
      currentMusic,
      isPlaying,
      playMusic,
      pauseMusic,
      musicCover,
      musicCard,
      audio,
      nextMusic,
      prevMusic,
      durationTime,
      currentTime
    }
  }
}
</script>